@import url(https://cdnjs.cloudflare.com/ajax/libs/weather-icons/2.0.12/css/weather-icons.min.css);
body{
    font-family: 'Poppins', sans-serif;
    background-color:#393E46;
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    overflow-x: hidden;
}
.main-heading{
    text-align: center;
    color: #F9ED69;
    padding-top: 5vh;
}
.search-container{
    height: 30vh;
    text-align: center;
    padding-top: 10vh;
}
.ip-btn,.submit-btn{
    border: 0;
    border-radius: 30px;
    padding: 0.25rem 0.5rem;
}
.submit-btn{
    margin-left: 1rem;
    padding: 0.25rem 0.5rem;
    background-color: #00ADB5;
}
.submit-btn:hover{
    padding: 0.5rem 0.75rem;
}
.row{
    text-align: center;
    color: #00ADB5;
}
.col{
    height: 30vh;
    margin: 1rem;
    border-radius: 50px;
    background: #393E46;
    box-shadow:  28px 28px 56px #2f333a,
                 -28px -28px 56px #434952;
}
.weather-icon{
    padding-top: 2rem;
    font-size: 3rem;
}
.city,.date{
    color: #00ADB5;
    text-align: center;
    margin: 2rem 0;
}
.date{
    color: #EEEEEE;
    opacity: 50%;
}
.mood{
    padding-top: 0.5rem;
}
.popOut,.mood{
    color: #EEEEEE;
}
.temp-stats{
    padding-top: 3rem;
}
.footer{
    margin-top: 7rem;
    color: #EEEEEE;
    text-align: center;
}
@media only screen and (min-width:768px){
    .col{
        height: 25vh;
    }
    .weather-icon{
        padding-top: 3rem;
    }
}
@media only screen and (min-width:1280px){
    .main-heading{
        font-size: 4rem;
    }
}